











































































































































































































































































































































































































.mf-prompt {
  width: 100%;
  .readline {
    width: 100%;
  }
  .breadcrumb {
    margin: 0;
    padding: 4px;
    color: white;
    /* text-shadow: 0 0 1px black; */
    li {
      /* font-weight: bold; */
      &:before {
        /* color: lightskyblue !important; */
        color: #3a3a92;
        margin: 0 !important;
        display: inline !important;
        font-size: 0.875rem !important;
        font-weight: bold !important;
      }
      a {
        color: #3a3a92;
        padding: 1px 2px;
        &:hover {
          background: rgba(255,255,255,0.25);
        }
      }
    }
    &.tmp {
      background: rgba(255,255,255,0.3);
      margin-right: -3px;
      border-top-left-radius: 4px;
      padding-right: 6px;
      li {
        a{
          color: #3f3f98;
        }
      }
    }
  }
  input {
    &.cli {
      padding: 4px 10px;
      height: auto;
      font-size: 14px;
      background: rgba(0,0,0,0.5);
      font-family: monospace;
      border: 1px solid skyblue;
      border-bottom-width: 0;
      border-right-width: 0;
      border-top-left-radius: 4px;
      box-shadow: inset 0 2px 4px rgba(0,0,0,0.25);
      &:focus {
        background: rgba(0,0,0,0.8);
      }
    }
  }
  .object-search {
    background: white;
    padding: 0 10px;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    > span {
      padding: 5px 0 0;
      color: steelblue;
    }
    >.multiselect {
      padding-right: 0;
      background: transparent;
      height: 32px;
      min-height: 32px;
      .multiselect__tags {
        padding-top: 4px;
        padding-left: 4px;
        height: 32px;
        min-height: 32px;
      }
      .multiselect__select {
        height: 32px;
      }
      .multiselect__placeholder {
        padding-top: 0;
        height: 24px;
        min-height: 24px;
        line-height: 24px;
        vertical-align: top;
      }
    }
    .btn {
      padding: 0 7px;
      height: 30px;
      line-height: 33px;
      &.btn-confirm {
        color: limegreen;
        &:hover {
          color: forestgreen;
        }
      }
      &.btn-cancel {
        color: red;
        &:hover {
          color: darkred;
        }
      }
    }
  }
  .btn-go {
    background: limegreen;
    color: white;
    height: 32px;
    border: 1px solid lightgreen;
    border-radius: 4px 0 0 0;
    border-bottom-width: 0;
    border-right-width: 0;
    line-height: 30px;
  }
  textarea.cli {
    line-height: normal;
    padding: 8px 10px;
    }
  }
